export default function swiper() {
  const ul = document.querySelector('.swiper ul')
  const lis = document.querySelectorAll('.swiper ul li')
  const img = document.querySelector('.swiper ul li img')
  const swiper = document.querySelector('.swiper')
  const first = ul.children[0].cloneNode(true)
  ul.appendChild(first)
  swiper.style.width = img.width + 'px'
  swiper.style.height = img.height + 'px'
  ul.style.width = img.width * (lis.length + 1) + 'px'
  let index = 0
  anmiton(ul, -0,50)
  function anmiton(obj, target, time = 0) {
    //清除以前定时器只保留当前定时器执行(节流)
    clearInterval(obj.timer)
    //缓存动画核心算法目标值-元素现在的偏移量在除以10
    obj.timer = setInterval(function () {
      let step = (target - obj.offsetLeft) / 10
      step = step > 0 ? Math.ceil(step) : Math.floor(step)

      if (obj.offsetLeft == target) {
        clearInterval(obj.timer)
        index++
     
        if(index == lis.length+1) {
          console.log(lis.length);
          ul.style.left = 0
          index = 0
        }
        anmiton(ul, -img.width * index,50)
      }
      obj.style.left = obj.offsetLeft + step + 'px'
    }, time)
  }
}